<template>
  <div class="flex h100">
    <div id="data-outer" class="border-bottom">
      <div class="container p-2">
        <div id="data-button-container" class="flex-row">
          <button id="add-data-btn" class="btn btn-info border btn-sm mx-1 flex-non-grow" type="button" tabindex="-1" v-on:click='addData'>新增水表</button>
          <div id="data-card-container">
            <router-link v-bind:to="'/watermeter/pkey/'+$route.params.pkey+ '/dkey/'+i.dkey" class="data-card card border mx-1 text-body" :class="[parseSelected(i)?'data-card-selected':'']" v-for="i in watermeterData" v-bind:key="i.dkey">
              <div class="h100">
                <div class="h40">
                  <div class="inline lg" v-html="parseDate(i.date)"></div>
                  <div class="inline sm pl-2">{{parseBW(i.bw)}}</div>
                  <button class="close" v-on:click="deleteData(i.dkey) ">
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
                <div class="h30">
                  <span class="badge badge-primary">D{{parseDex(i.dex)?parseDex(i.dex):'_'}}W</span>
                  <span class="badge" :class="[i.wm.lipid_perKg>0?'badge-warning':'badge-light, text-light-grey']">Lipid</span>
                  <span class="badge" :class="[i.pn.k_perKg>0?'badge-success':'badge-light, text-light-grey']">K</span>
                  <span class="badge" :class="[i.pn.ca_perKg>0?'badge-danger':'badge-light, text-light-grey']">Ca</span>
                </div>
                <div class="h30 sm text-right pr-1 text-muted pt-1">
                  {{i.updateBy}}, {{parseUpdate(i.lastUpdate)}}
                </div>

              </div>

            </router-link>
          </div>
        </div>
      </div>
    </div>
    
    <div class="container p-2" v-show="!dkeyExist">
      <div class="text-center mt-3">
      <h4>病人基本資料</h4>
    </div>
      <div class="row mb-2">
        <div class="col">
          <div class="input-group input-group-sm">
            <div class="input-group-prepend">
              <span class="input-group-text" id="inputGroup-sizing-sm">床號</span>
            </div>
            <input type="text" class="form-control" v-model="patientData.bedno" aria-label="Small" aria-describedby="inputGroup-sizing-sm">
          </div>
        </div>
        <div class="col">
          <div class="input-group input-group-sm">
            <div class="input-group-prepend">
              <span class="input-group-text" id="inputGroup-sizing-sm">名稱</span>
            </div>
            <input type="text" class="form-control" v-model="patientData.name" aria-label="Small" aria-describedby="inputGroup-sizing-sm">
          </div>
        </div>
        <div class="col">
          <div class="input-group input-group-sm">
            <div class="input-group-prepend">
              <span class="input-group-text" id="inputGroup-sizing-sm">病歷號</span>
            </div>
            <input type="text" class="form-control" v-model="patientData.hisid" aria-label="Small" aria-describedby="inputGroup-sizing-sm">
          </div>
        </div>
        <div class="col-4">
          <button type="button" tabindex="-1" class="btn btn-sm" v-bind:class="[dataChanged?'btn-primary':'btn-light']" v-on:click="savePatientData">儲存</button>
          <button type="button" tabindex="-1" class="btn btn-sm" v-bind:class="[dataChanged?'btn-warning':'btn-light']" v-on:click="giveUpChange">捨棄</button>
        </div>
      </div>
    </div>

    <router-view class="flex-grow flex" v-if="dkeyExist"></router-view>
  </div>
</template>

<script>
import firebase from "firebase";
import util from "../util";
import $ from "jquery";
import $$ from "jquery-mousewheel";

let database;
export default {
  name: "watermeterPatient",
  data: function() {
    return {
      patientData: {
        name: "",
        hisid: "",
        bedno: "",
        active: ""
      },
      watermeterData: [],
      newData: "",
      lastUpdate: "",
      updateBy: "",
      patientImage: {
        name: "",
        hisid: "",
        bedno: "",
        active: ""
      }
    };
  },
  methods: {
    addData: function() {
      let vm = this;
      database = firebase.database();
      let PatientRef = database.ref(
        "/watermeter/" + vm.$route.params.pkey + "/watermeterData/"
      );
      let currentUser = firebase.auth().currentUser.email.split("@")[0];
      let dkey = PatientRef.push({
        //初始WM資料
        bw: "",
        date: util.getToday(),
        dex: 0,
        note: "",
        active: true,
        updateBy: currentUser,
        lastUpdate: util.getNow(),
        wm: {
          bw: "",
          feed_type: "",
          feed_amount: "",
          feed_freq: "",
          feed_total: "",
          continuousVolume: "",
          bolusVolume: "",
          lipid_perKg: "",
          lipid_perDay: "",
          lipid_perHour: "",
          wm_summary_volume: "-",
          wm_summary_daily: "-",
          wm_summary_gir: "-",
          wm_summary_na: "-"
        },
        pn: {
          bw: "",
          pn_desired: "",
          pn_priming: 30,
          pn_prescribe: "",
          aa_perKg: 3,
          aa_conc: "",
          aa_prescribe: "",
          glu_perKg: "",
          glu_conc: 10,
          glu_prescribe: "",
          na_perKg: "",
          na_conc: "",
          na_prescribe: "",
          k_perKg: "",
          k_conc: "",
          k_prescribe: "",
          ca_perKg: "",
          ca_conc: "",
          ca_prescribe: "",
          p_perKg: "",
          p_conc: "",
          p_prescribe: "",
          mg_perKg: "",
          mg_conc: "",
          mg_prescribe: "",
          te_perKg: 0.5,
          te_prescribe: "",
          lyop_perKg: 0.25,
          lyop_prescribe: "",
          pn_summary_volume: "-",
          pn_summary_rate: "-",
          pn_summary_gir: "-",
          pn_summary_osm: "-",
          pn_error: ""
        }
      });
    },
    deleteData: function(dkey) {
      let vm = this;
      database = firebase.database();
      let deleteRef = database.ref(
        "/watermeter/" +
          this.$route.params.pkey +
          "/watermeterData/" +
          dkey +
          "/"
      );
      deleteRef.remove();
    },
    loadPatientData: function() {
      let vm = this;
      let patient = this.$store.getters.currentPatient;
      if (!patient) {
        return;
      }
      let patientData = vm.patientData;
      let patientImage = vm.patientImage;
      patientImage.bedno = patientData.bedno = patient.bedno;
      patientImage.hisid = patientData.hisid = patient.hisid;
      patientImage.name = patientData.name = patient.name;
      patientImage.active = patientData.active = patient.active;
      vm.lastUpdate = patient.lastUpdate;
      vm.updateBy = patient.updateBy;
      vm.watermeterData = patient.watermeterData;
    },
    savePatientData: function() {
      let vm = this;
      let currentUser = firebase.auth().currentUser.email.split("@")[0];
      database = firebase.database();
      let saveRef = database.ref("/watermeter/" + this.$route.params.pkey);
      let patientData = vm.patientData;
      saveRef.update({
        bedno: patientData.bedno,
        hisid: patientData.hisid,
        name: patientData.name,
        active: patientData.active,
        lastUpdate: util.getNow(),
        updateBy: currentUser
      });
      vm.loadPatientData();
    },
    giveUpChange: function() {
      let vm = this;
      let patientData = vm.patientData;
      let patientImage = vm.patientImage;
      patientData.bedno = patientImage.bedno;
      patientData.hisid = patientImage.hisid;
      patientData.name = patientImage.name;
      patientData.active = patientImage.active;
    },
    parseSelected(input) {
      return input.dkey == this.$route.params.dkey;
    },
    parseDate: function(inputDate) {
      let year = inputDate.split("-")[0] || "";
      let month = inputDate.split("-")[1] || "";
      let date = inputDate.split("-")[2] || "";
      if (!year || !month || !date) {
        return inputDate;
      }
      return (
        Number(year) +
        '<span class="sm text-muted">/</span>' +
        Number(month) +
        '<span class="sm text-muted">/</span>' +
        Number(date)
      );
    },
    parseBW: function(inputBW) {
      let match = inputBW.match(/[0-9]+/);
      if (!match) {
        return "";
      } else {
        return match[0] + "g";
      }
    },
    parseDex: function(inputDex) {
      return inputDex;
    },
    parseUpdate(inputDateTime) {
      if (!inputDateTime) {
        return "";
      }
      let split = inputDateTime.split(" ");
      let date = split[0].split("-").map(x => Number(x));
      date.splice(0, 1);
      date = date.join("/");
      let time = split[1].split(":");
      time.splice(2, 1);
      time = time.join(":");
      return date + " " + time;
    }
  },
  computed: {
    dataChanged: function() {
      let vm = this;
      let patientData = vm.patientData;
      let patientImage = vm.patientImage;
      return (
        patientImage.bedno != patientData.bedno ||
        patientImage.hisid != patientData.hisid ||
        patientImage.name != patientData.name ||
        patientImage.active != patientData.active
      );
    },
    dkeyExist: function() {
      return this.$store.getters.dkeyExist;
    }
  },
  watch: {
    $route() {
      this.loadPatientData();
    },
    "$store.getters.currentPatient"() {
      if (!this.dataChanged) {
        this.loadPatientData();
      }
    }
  },
  mounted() {
    this.loadPatientData();
    $("#data-card-container").mousewheel(function(event, delta) {
      this.scrollLeft -= delta * 30;
      event.preventDefault();
    });
  }
};
</script>

<style scoped>
#active-btn {
  width: 90px;
}
textarea {
  min-height: 55px;
}
#data-outer {
  height: 100px;
}
#data-button-container {
  height: 100px;
}
#data-card-container {
  width: 100%;
  height: 90px;
  white-space: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
}
#add-data-btn {
  height: 70px;
}
.data-card {
  width: 150px;
  display: inline-block;
  height: 70px;
  text-decoration: none !important;
  padding-left: 5px;
}
.data-card-selected {
  border: 1px solid black !important;
  background-color: rgb(230, 255, 255);
}
.data-card:hover {
  background-color: whitesmoke;
  background-color: rgb(240, 255, 255);
}

.clear-both {
  display: block;
}
.flex-row {
  display: flex;
  flex-direction: row;
}
.flex {
  display: flex;
  flex-direction: column;
}
.flex-non-grow {
  flex: 0 0;
}
.flex-grow {
  flex: 1 0;
}
.h100 {
  height: 100%;
}
.h20 {
  height: 20%;
}
.h30 {
  height: 30%;
}
.h40 {
  height: 40%;
}
.h50 {
  height: 50%;
}
.w100 {
  width: 100%;
}
.w50 {
  width: 40%;
}
.inline {
  display: inline-block;
}
.sm {
  font-size: 0.8em;
}
.lg {
  font-size: 1.1em;
}
.text-light-grey{
  color: rgb(180,180,180)
}
</style>



