<template>
  <div id="app">
    <nav class="navbar navbar-expand-lg navbar-light justify-content-between">
      <router-link to="/" class="navbar-brand">VGHTPE PedTool</router-link>
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">
          <li id="btn-watermeter" class="nav-item active p-0">
            <router-link to="/watermeter" class="nav-link">WaterMeter</router-link>
          </li>
        </ul>
      </div>
      <form class="form-inline">
        <div class="mr-1">{{loggedEmail}}</div>
        <button class="btn btn-light btn-sm border-dark my-2 my-sm-0" v-if="logged" v-on:click='logout'>Log Out</button>
      </form>
    </nav>
    <router-view id="content-box" />
  </div>
</template>

<script>
import firebase from "firebase";
export default {
  name: "App",
  data: function() {
    return {
      logged: false,
      loggedID: "",
      loggedEmail: ""
    };
  },
  methods: {
    logout: function() {
      let vm = this;
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace("/login");
        });
    },
    checkAuth: function(user) {
      let vm = this;
      if (user) {
        vm.logged = true;
        vm.loggedEmail = user.email.split("@")[0];
      } else {
        vm.logged = false;
        vm.loggedEmail = "";
      }
    }
  },
  mounted: function() {
    let vm = this;
    vm.checkAuth(firebase.auth().currentUser);
    firebase.auth().onAuthStateChanged(function(user) {
      vm.checkAuth(user);
    });
  }
};
</script>

<style lang="scss">
@import "bootstrap/scss/bootstrap.scss";
#app {
  font-family: "Avenir", Helvetica, Arial, "Microsoft JhengHei", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  max-height: 100vh;
  overflow: hidden;
  justify-content: space-between;
}
.no-gutters {
  margin-right: 0;
  margin-left: 0;

  > .col,
  > [class*="col-"] {
    padding-right: 0;
    padding-left: 0;
  }
}
.navbar {
  background-color: rgb(180, 209, 235);
  flex: 0 0 auto;
}
#content-box {
  flex: 1 1 auto;
  overflow-y: hidden;
}
#btn-watermeter{
  border-radius: 10px;
  padding-top:2px; 
  padding-bottom: 2px;
  background-color: rgb(129, 162, 190);
  transition: all 0.3s;
}
#btn-watermeter:hover {
  background-color: rgb(135, 173, 206);
}
</style>



