<template>
  <div class="outer">
    <form id="novalidatedform" novalidate />
    <div class="patient-list-container border-right">
      <div class="input-group add-patient p-3 border-bottom">
        <input type="text" class="form-control" placeholder="Search" v-model="searchPatient">
        <div class="input-group-append">
          <button class="btn btn-outline-secondary" type="button" v-on:click='addPatient'>新增病人</button>
        </div>
      </div>
      <div class="patient-list p-3">
        <div class="mb-2" v-for="i in patientList" :key="i.pkey">
          <router-link class="card patient-card" :class="[parseSelected(i)?'patient-card-selected':'']" v-bind:to="'/watermeter/pkey/'+i.pkey">
            <div>
              <div class="inline w80">
                {{i.bedno}} {{i.name}}
              </div>
              <button class="close" v-on:click="deletePatient(i.pkey)">
                <span>&times;</span>
              </button>
              <div class="text-xs text-muted">{{i.number}}筆資料</div>
              <div class="text-xs text-muted">{{i.updateBy}}@{{parseUpdate(i.lastUpdate)}}</div>
            </div>
          </router-link>
        </div>
      </div>
    </div>
    <div class="patient">
      <div class="m-3" v-if="!pkeyExist">
        <h4 class="pl">水表/PN開立計算機簡易操作說明</h4>
        <ol>
          <li>從左邊選取病人或新增病人</li>
          <li>編輯病人基本資料(方便查找用)，編輯完要按儲存才會生效</li>
          <li>選定病人後，從上方水表清單新增/選取水表</li>
          <li>先輸入體重、日期等基本資料</li>
          <li>編輯水表內容，編輯完記得按儲存才會生效，</li>
        </ol>
        <h4 class="pl mt-3">功能及特色</h4>
        <ol>
          <li>需要帳號密碼才能存取資料庫</li>
          <li>採同步編輯模式，在不同裝置可看到同步的資料，但多人同時儲存同一筆水表可能會有覆蓋問題</li>
          <li>水表為用來整理病人所有input，方便交班及計算PN應該開立的水量</li>
          <li class="font-weight-bold text-primary">若將水表內連續滴注之項目命名為"PN"，則總量會自動跟右邊的TPN sheet連結</li>
          <li>TPN功能之"prescribe"欄位為在醫囑系統內需開立的劑量</li>
          <li>本表的建議值是為早產兒及嬰兒設計</li>
        </ol>
      </div>
      <div v-if="pkeyExist" class="h100">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import util from "../util.js";
let database;
export default {
  name: "waterMeter",
  data() {
    return {
      searchPatient: ""
    };
  },
  computed: {
    patientList: function() {
      return this.$store.state.patientList;
    },
    pkeyExist: function() {
      return this.$store.getters.pkeyExist;
    }
  },
  methods: {
    addPatient: function() {
      let vm = this;
      let waterMeterRef = database.ref("/watermeter/");
      let currentUser = firebase.auth().currentUser.email.split("@")[0];
      waterMeterRef.push({
        hisid: "",
        name: "新病人",
        bedno: "",
        active: true,
        lastUpdate: util.getNow(),
        updateBy: currentUser,
        watermeter: []
      });
    },
    deletePatient: function(pkey) {
      let patientRef = database.ref("/watermeter/" + pkey + "/");
      patientRef.remove();
    },
    setRoute(route) {
      let vm = this;
      vm.$store.commit("setRoute", route);
    },
    parseUpdate(inputDateTime) {
      let split = inputDateTime.split(" ");
      let date = split[0].split("-").map(x => Number(x));
      date.splice(0, 1);
      date = date.join("/");
      let time = split[1].split(":");
      time.splice(2, 1);
      time = time.join(":");
      return date + " " + time;
    },
    parseSelected(input) {
      return input.pkey == this.$route.params.pkey;
    }
  },
  beforeRouteUpdate(to, from, next) {
    let vm = this;
    vm.setRoute(to);
    if (to.params.pkey && vm.patientList.find(x => x.pkey == to.params.pkey)) {
      next();
    } else if (!to.params.pkey) {
      next();
    } else {
      next("/watermeter");
    }
  },
  mounted() {
    let vm = this;
    vm.setRoute(vm.$route);
    database = firebase.database();
    let waterMeterRef = database.ref("/watermeter/");
    waterMeterRef.on("value", function(snapshot) {
      vm.$store.commit("setSnapshot", snapshot.val());
    });
  }
};
</script>

<style scoped>
.outer {
  width: 100%;
  height: 100%;
  display: flex;
}
.patient-list-container {
  width: 20%;
  border-color: grey;
  border-width: 2px;
  width: 200px;
  display: flex;
  flex-direction: column;
}
.add-patient {
  flex: 0 0 auto;
}
.patient-list {
  overflow-x: hidden;
  overflow-y: scroll;
  flex: 1 1 auto;
}
.patient {
  width: 80%;
  flex: 8 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.patient-card {
  text-decoration: none !important;
}
.patient-card-selected {
  border: 1px solid black !important;
  background-color: rgb(230, 255, 255);
}
.patient-card:hover {
  background-color: rgb(240, 255, 255);
}
.h100 {
  height: 100%;
}
.w20 {
  width: 20%;
}
.w80 {
  width: 80%;
}
.inline {
  display: inline-block;
}
.text-xs {
  font-size: 0.8em;
}
</style>



